<template>
  <div class="main">
    <div>
      <h3 class="inline">Nodes:</h3> {{ nodes.length }} total
    </div>
    <div class="flex justify-between">
      <div>
        <div class="w20 inline"><el-input placeholder="Type something" v-model="sk">
            <i slot="prefix" class="el-input__icon el-icon-search"></i>
          </el-input> </div>
        <el-button type="" @click="search">search Across</el-button>
      </div>
      <div>
        <el-button type="success" icon="el-icon-plus" @click="showaddfn" circle></el-button>
        <el-button type="primary" icon="el-icon-edit" @click="showdialogfn" circle></el-button>
        <el-button type="danger" icon="el-icon-delete" @click="delhost" circle></el-button>
      </div>
    </div>
    <div class="main01">

      <div class="flex nodeholder ">
        <div style="width: 12px;" />
        <div class="p10" style="">
          <div class="flex flex-row justify-content-between w80">
            <div>主机名</div>
            <div v-if="skmode">ip</div>
            <div v-if="skmode">系统</div>
            <div v-if="skmode">dc</div>
            <div v-if="skmode">env</div>
            <div v-if="skmode">appcode</div>
            <div v-if="skmode">approle</div>
            <div>owner</div>
            <div>cpu</div>
            <div>mem</div>
            <div v-if="skmode">uptime</div>
			
          </div>
        </div>
      </div>


      <div class="flex nodeholder " v-for="(i) of  nodes " :key="i.host_id">
        <input type="checkbox" v-model="i.checked">
        <div class="p10">
          <div class="flex flex-row justify-content-between w80">
            <div  @click="showDetail(i)">{{ i.hostname }}</div>
            <div v-if="skmode">{{ i.ip_address }}</div>
            <div v-if="skmode">{{ i.os }}</div>
            <div v-if="skmode"> {{ i.dc }}</div>
            <div v-if="skmode"> {{ i.env }}</div>
            <div v-if="skmode"> {{ i.appcode }}</div>
            <div v-if="skmode"> {{ i.approle }}</div>
            <div> {{ i.owner }}</div>
            <div>{{ i.cpu }}</div>
            <div>{{ i.mem }}</div>
            <div v-if="skmode">{{ i.uptime }}</div>
          </div>
          <div>
            <el-badge style="margin-left:5px" v-for="(b, index) of  i.tags " :key="index" :value="b" class="item"
              type="primary" />
            <div style="margin-left: 10px; " class="inline"> <el-button size="smal" style="border: 0;" circle
                icon="el-icon-plus" @click="ntag.host_id = i.host_id, ntagshowdialog = true"></el-button></div>
          </div>
        </div>
      </div>
    </div>
    <el-dialog :title="formTitle" :visible.sync="showdialog" width="40%">
      <div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">主机名</span>
          <el-input placeholder="主机名" v-model="fmdata.hostname">
          </el-input>
        </div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">ip</span>
          <el-input placeholder="ip" v-model="fmdata.ip_address">
          </el-input>
        </div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">系统</span>
          <el-input placeholder="系统" v-model="fmdata.os">
          </el-input>
        </div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">dc</span>
          <el-input placeholder="dc" v-model="fmdata.dc">
          </el-input>
        </div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">环境</span>
          <el-input placeholder="环境" v-model="fmdata.env">
          </el-input>
        </div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">appcode</span>
          <el-input placeholder="appCode" v-model="fmdata.appcode">
          </el-input>
        </div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">app角色</span>
          <el-input placeholder="app角色" v-model="fmdata.approle">
          </el-input>
        </div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">所有者</span>
          <el-input placeholder="所有者" v-model="fmdata.owner">
          </el-input>
        </div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">cpu</span>
          <el-input placeholder="cpu" v-model="fmdata.cpu">
          </el-input>
        </div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">内存</span>
          <el-input placeholder="内存" v-model="fmdata.mem">
          </el-input>
        </div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">启动时间</span>
          <el-input placeholder="启动时间" v-model="fmdata.uptime">
          </el-input>
        </div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">tags</span>
          <el-input placeholder="tags" v-model="fmdata.tagstr">
          </el-input>
        </div>

      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showdialog = false">Cancel</el-button>
        <el-button v-if="showUpdate" type="primary" @click="doupdate">Confirm</el-button>
      </span>
    </el-dialog>

    <el-dialog title="编辑" :visible.sync="ntagshowdialog" width="40%">
      <div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">key</span>
          <el-input placeholder="key" v-model="ntag.k">
          </el-input>
        </div>
        <div class="demo-input-suffix">
          <span class="demo-input-label">value</span>
          <el-input placeholder="value" v-model="ntag.v">
          </el-input>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="ntagshowdialog = false">Cancel</el-button>
        <el-button type="primary" @click="doupdatetag">Confirm</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "c-liquid-fill",
  props: {
  },
  data() {
    return {
		formTitle:"编辑",
      sk: "", total: 0, nodes: [], checkedid: -1,
      skmode: false,
      showdialog: false,
      fmdata: {},
      ntagshowdialog: false,
      ntag: {},
	  showUpdate:true,
    }
  },
  mounted() {
    this.getAllNodes()
  },
  components: {
  },
  computed: {
  },
  methods: {
    addtag(i) {
      console.log(i)
    },
    doupdatetag() {

      this.$axios.put("/hosts/tag", {host_id:this.ntag.host_id, [this.ntag.k]: this.ntag.v }).then(() => {
        this.getAllNodes()
        this.ntagshowdialog = false
      })
    },
    doupdate() {
      try {
        let tags = JSON.parse(this.fmdata.tagstr)
        if (Object.keys(tags).length >= 1 || this.fmdata.tagstr == "{}") {
          this.fmdata.tags = tags;
          delete this.fmdata.tagstr
        } else {
          throw new Error();
        }

      } catch (e) {
        return alert("tags 字段需要为json格式")

      }
      if (this.fmdata.host_id) {
        this.showdialog = false
        this.$axios.put("/hosts/" + this.fmdata.host_id, this.fmdata).then(() => {
          this.getAllNodes()
        })
      } else {
        this.showdialog = false
        this.$axios.post("/hosts", this.fmdata).then(() => {
          this.getAllNodes()
        })
      }
    },
    showaddfn() {
      this.showdialog = true
      this.fmdata = {}

    },
    showdialogfn() {
      let cknodes = this.nodes.filter(i => i.checked)
      if (cknodes.length !== 1) {
        return alert('请选择单个node')
      } else {
		  this.formTitle = "编辑";
		  this.showUpdate = true;
        this.showdialog = true
        this.fmdata = cknodes[0]
      }

    },
	
	showDetail(i){
		this.showUpdate = false;
		this.formTitle = "详情";
		this.showdialog = true;
		this.fmdata = i;
	},
    delhost() {
      let cknodes = this.nodes.filter(i => i.checked)
      if (cknodes.length !== 1) {
        return alert('请选择单个node')
      }
      this.$confirm('确定删除？')
        .then(() => {
          this.$axios.delete(`/hosts/${cknodes[0].host_id}`).then(() => {
            this.getAllNodes()
          })
        })
    },
    changecheckedid(i) {
      this.checkedid = i
    },
    search() {

      if (this.sk) {
        this.skmode = true;
        this.$axios.post(`/hosts/${this.sk}`).then(d => {
          if (d.data.error_code == 0) {
            this.nodes = [d.data.data]
          }
        })
      } else {
        this.skmode = false;
        this.getAllNodes()
      }

    },
    getAllNodes() {
      this.$axios.get("/hosts").then(d => {
        console.log(d.data)
        if (d.data.error_code == 0) {
          this.nodes = d.data.data.map(i => ({
            ...i,
            checked: false,
            tagstr: JSON.stringify(i.tags)
          }))
        }
      })
    }
  },
};
</script>

